::-webkit-scrollbar {
	width: 7px;
	height: 7px;
}

/* Track */

::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px #afafaf;
	box-shadow: inset 0 0 6px #afafaf;
	-webkit-border-radius: 10px;
	border-radius: 10px;
}

/* Handle */

::-webkit-scrollbar-thumb {
	-webkit-border-radius: 10px;
	border-radius: 10px;
	background: #78c3ff;
	-webkit-box-shadow: inset 0 0 6px #004175;
	box-shadow: inset 0 0 6px #004175;
}

