.play-button {
	position: absolute;
	z-index: 2;
	top: 50%;
	left: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 8px;
	color: #ffffff;
	background: #3F51B5;
	border-radius: 50%;
	-webkit-animation: beat 5s infinite;
	animation: beat 2s infinite;
	cursor: pointer;
	transition: box-shadow 0.3s;
}

.play-button:hover {
	box-shadow: 0px 0px 5px 5px #3F51B5;
}

@keyframes beat {
	12% {
		-webkit-transform: scale(1.03);
		transform: scale(1.03);
		box-shadow: 0 0 5px 5px #3F51B5;
	}
	45% {
		-webkit-transform: scale(1);
		transform: scale(1);
		box-shadow: 0 0 12px 12px #3F51B5;
	}
}

.hero-actions:hover {
	transform: scale(1.02);
	transition: transform 0.4s;
	cursor: pointer;
}

